import React, { useEffect, useState } from 'react';
import { Messaging } from '../../types';
import { PanelProps } from '../PanelProps';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Button from '@material-ui/core/Button';
import { createStyles, makeStyles, withStyles } from '@material-ui/core/styles';
import { Slider } from '@material-ui/core';

interface SliderProps extends PanelProps {
  customStyles: any;
}

export default function SliderPanel(props: SliderProps) {
  let [_CustomSlider, setCustomSlider] = useState<any>(null);
  const {
    _client,
    writable,
    topicProtocols,
    fieldConfig,
    isRunning,
    setIsRunning,
    dataValue,
    options,
    customStyles,
  } = props;
  const [originalValue, setOriginalValue] = useState(0);
  const [currentValue, setCurrentValue] = useState(0);

  const useStyles = makeStyles(() =>
    createStyles({
      slider: {
        flexDirection: 'column',
        padding: '10px',
        height: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      },
      button: { ...customStyles.button, width: '70px' },
    })
  );
  const classes = useStyles();

  useEffect(() => {
    setOriginalValue(+dataValue?.text);
    setCurrentValue(+dataValue?.text);
    setIsRunning(false);
  }, [dataValue]);

  const onSetValue = () => {
    if (!_client.current) {
      return;
    }
    if (_client.current.disconnected) {
      _client.current.reconnect();
    }
    setIsRunning(true);
    setOriginalValue(currentValue);
    topicProtocols.forEach(({ topic, messaging }) => {
      if (messaging === Messaging.REQ_RES) {
        topic += '/req';
      }
      _client.current.publish(topic, JSON.stringify({ value: currentValue }), { retain: true });
    });
  };

  const onResetValue = () => {
    setCurrentValue(originalValue);
  };

  const ThumbComponent = (props: any) => {
    return (
      <span {...props}>
        <span style={{ fontSize: 10 }}>{currentValue}</span>
      </span>
    );
  };

  useEffect(() => {
    setCustomSlider(
      withStyles({
        root: {
          height: 6,
          color: options?.overrideSliderSettings ? options?.sliderColor : customStyles?.slider?.color,
        },
        thumb: {
          marginTop: -8,
          marginLeft: -12,
          width: 24,
          height: 24,
          backgroundColor: '#fff',
          border: '2px solid currentColor',
          '&.MuiSlider-thumb.Mui-disabled': {
            marginTop: -8,
            marginLeft: -12,
            width: 24,
            height: 24,
            border: '50%',
          },
        },
        valueLabel: {
          left: 'calc(-50% + 4px)',
          top: '-32px',
        },
        track: {
          height: 6,
          borderRadius: 3,
        },
        rail: {
          height: 6,
          borderRadius: 3,
        },
      })(Slider)
    );
  }, [customStyles.slider.color, options]);

  return (
    <div className={classes.slider}>
      <ButtonGroup disableElevation variant="contained">
        <Button disabled={!writable} onClick={onSetValue} className={classes.button}>
          Set
        </Button>
        <Button disabled={currentValue === originalValue} onClick={onResetValue} className={classes.button}>
          Reset
        </Button>
      </ButtonGroup>
      {_CustomSlider && (
        <_CustomSlider
          ThumbComponent={ThumbComponent}
          min={fieldConfig?.min || 0}
          max={fieldConfig?.max || 100}
          defaultValue={originalValue}
          value={currentValue}
          valueLabelDisplay="on"
          onChange={(e: any, value: any) => {
            let val = 0;
            if (typeof value !== 'number') {
              val = value[0];
            } else {
              val = value;
            }
            setCurrentValue(val);
          }}
          disabled={isRunning}
        />
      )}
    </div>
  );
}
