import React, { useEffect, useState } from 'react';
import { Messaging } from '../../types';
import { PanelProps } from '../PanelProps';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Button from '@material-ui/core/Button';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import { Input } from '@grafana/ui';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import { IconButton } from '@material-ui/core';

interface SliderProps extends PanelProps {
  customStyles: any;
}

export default function NumericFieldWriterPanel(props: SliderProps) {
  const { _client, writable, topicProtocols, fieldConfig, isRunning, setIsRunning, dataValue, customStyles } = props;
  const [originalValue, setOriginalValue] = useState(0);
  const [currentValue, setCurrentValue] = useState(0);

  const useStyles = makeStyles(() =>
    createStyles({
      root: {
        flexDirection: 'column',
        padding: '10px',
        height: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      },
      blankLine: {
        marginTop: '6px',
      },
      line: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
      },
      button: { ...customStyles.button, width: '70px' },
      input: { width: '140px' },
      arrowButton: {
        marginLeft: '4px',
        height: '32px',
        width: '32px',
      },
    })
  );

  const classes = useStyles();

  useEffect(() => {
    setOriginalValue(+dataValue?.text);
    setCurrentValue(+dataValue?.text);
    setIsRunning(false);
  }, [dataValue]);

  const onSetValue = () => {
    if (!_client.current) {
      return;
    }
    if (_client.current.disconnected) {
      _client.current.reconnect();
    }
    setIsRunning(true);
    setOriginalValue(currentValue);
    topicProtocols.forEach(({ topic, messaging }) => {
      if (messaging === Messaging.REQ_RES) {
        topic += '/req';
      }
      _client.current.publish(topic, JSON.stringify({ value: currentValue }), { retain: true });
    });
  };

  const onResetValue = () => {
    setCurrentValue(originalValue);
  };

  return (
    <div className={classes.root}>
      <div className={classes.line}>
        <ButtonGroup disableElevation variant="contained">
          <Button disabled={!writable} onClick={onSetValue} className={classes.button}>
            Set
          </Button>
          <Button disabled={currentValue === originalValue} onClick={onResetValue} className={classes.button}>
            Reset
          </Button>
        </ButtonGroup>
        <IconButton
          className={classes.arrowButton}
          onClick={() => {
            if (currentValue + 1 <= (fieldConfig?.max || 100)) {
              setCurrentValue(currentValue + 1);
            }
          }}
          aria-label="add"
          color="primary"
        >
          <ArrowUpwardIcon fontSize="inherit" />
        </IconButton>
      </div>
      <div className={classes.blankLine} />
      <div className={classes.line}>
        <Input
          className={classes.input}
          type="number"
          min={fieldConfig?.min || 0}
          max={fieldConfig?.max || 100}
          defaultValue={originalValue}
          value={currentValue}
          onChange={(e: any) => {
            setCurrentValue(+e.target.value);
          }}
          disabled={isRunning}
        />
        <IconButton
          className={classes.arrowButton}
          onClick={() => {
            if (currentValue - 1 >= (fieldConfig?.min || 0)) {
              setCurrentValue(currentValue - 1);
            }
          }}
          aria-label="subtract"
          color="primary"
        >
          <ArrowDownwardIcon fontSize="inherit" />
        </IconButton>
      </div>
    </div>
  );
}
