import React, { useEffect, useRef, useState } from 'react';
import { getDisplayProcessor, PanelProps } from '@grafana/data';
import {
  BISettingsProps,
  ButtonColorSettings,
  PanelType,
  SimpleOptions,
  SliderColorSettings,
  SwitchColorSettings,
  TextSettings,
  TopicProtocol,
} from 'types';
import { css, cx } from 'emotion';
import { Spinner, stylesFactory } from '@grafana/ui';
import { getDataSourceSrv } from '@grafana/runtime';
import SliderPanel from './panels/Slider';
import SwitchPanel from './panels/Switch';
import MultiSwitchPanel from './panels/MultiSwitch';
import SingleStatPanel from './panels/SingleStat';
import NumericFieldWriterPanel from './panels/NumericFieldWriter';
import { Z_INDEX_BACKGROUND, Z_INDEX_OVERLAY, Z_INDEX_WRITER } from './constants';

interface Props extends PanelProps<SimpleOptions> {}

export const BasePanel: React.FC<Props> = props => {
  let _client: any = useRef(null);

  const [switchColorSettings, setSwitchColorSettings] = useState<SwitchColorSettings>({
    switchTrueColor: '#52D869',
    switchFalseColor: '#7E7E7E',
  });
  const [buttonStyle, setButtonStyle] = useState<ButtonColorSettings>({
    activeButtonColor: '#303F9F',
    activeButtonTextColor: '#E0E0E0',
    inactiveButtonColor: '#E0E0E0',
    inactiveButtonTextColor: '#AEAEAE',
    hoverButtonColor: '#30409E',
    hoverButtonTextColor: '#E0E0E0',
  });
  const [sliderColorSettings, setSliderColorSettings] = useState<SliderColorSettings>({
    sliderColor: '#303F9F',
  });
  const [textSettings, setTextSettings] = useState<TextSettings>({
    textSize: 40,
    unitSize: 30,
    unitColor: '#000',
  });
  const [_BISettings, setBISettings] = useState<BISettingsProps>({
    opacity: 0,
    scale: 0,
    xPosition: 0,
    yPosition: 0,
  });

  const [opacity, setOpacity] = useState(0);
  const [scale, setScale] = useState(0);
  const [xPosition, setXPosition] = useState(0);
  const [yPosition, setYPosition] = useState(0);

  const { options, fieldConfig, data, width, height } = props;
  const { backgroundImageURL, overrideBISettings } = options;

  // TODO: revert back
  // const [writable, setWritable] = useState(false);
  const [writable] = useState(true);
  const [value, setValue] = useState<any>({});
  const [isRunning, setIsRunning] = useState(false);
  const [topicProtocols, setTopicProtocols] = useState<TopicProtocol[]>([]);
  const [dataSources, setDataSources] = useState<any>([]);

  /*
  TODO: revert back
  useEffect(() => {
    (async () => {
      try {
        const orgResponse = await fetch('/api/org');
        const org = await orgResponse.json();
        const userOrgsResponse = await fetch('/api/user/orgs');
        const userOrgs = await userOrgsResponse.json();
        const currentUserOrg = userOrgs.find((userOrg: any) => userOrg.orgId === org.id);
        setWritable(currentUserOrg.role !== 'Viewer');
      } catch (e) {}
    })();
  }, []);*/

  useEffect(() => {
    if (!overrideBISettings) {
      setOpacity(_BISettings.opacity);
      setScale(_BISettings.scale);
      setXPosition(_BISettings.xPosition);
      setYPosition(_BISettings.yPosition);
    } else {
      setOpacity(options?.opacity || 30);
      setScale(options?.scale || 30);
      setXPosition(options?.xPosition || 100);
      setYPosition(options?.yPosition || 0);
    }
  }, [overrideBISettings, _BISettings, options]);

  useEffect(() => {
    const values = props?.data.series[0].fields[1].values.toArray();
    const rawValue = values.pop();
    const value = props?.data?.series[0]?.fields[1]?.display?.(rawValue);

    // To calculate non-mapped converted standard output (it includes decimal, unit conversion but not mapping)
    const displayProcessorWithoutMapping = getDisplayProcessor({
      field: {
        ...props.data.series[0].fields[1],
        config: {
          ...props.data.series[0].fields[1].config,
          mappings: [],
        },
      },
    });
    const valueWithoutMapping = displayProcessorWithoutMapping(rawValue);
    setValue({ ...value, suffix: valueWithoutMapping.suffix });
    setIsRunning(false);
  }, [data]);

  useEffect(() => {
    const targets: any[] = data?.request?.targets || [];
    const mqttValidConfig: any[] = targets.filter(target => !!target.topic);
    const topicsProtocols$: TopicProtocol[] = mqttValidConfig.map(target => ({
      topic: target.topic,
      messaging: target.messaging,
    }));
    setTopicProtocols(topicsProtocols$);
    if (mqttValidConfig.length) {
      const dataSources$: any[] = mqttValidConfig.map(x => x.datasource);
      if (JSON.stringify(dataSources) !== JSON.stringify(dataSources$)) {
        setDataSources(dataSources$);
      }
    }
  }, [data]);

  useEffect(() => {
    for (const dataSource of dataSources) {
      let match = false;
      getDataSourceSrv()
        .get(dataSource)
        .then(res => {
          if (res.meta.id === 'nubeio-mqtt-data-source') {
            // @ts-ignore
            const { client, switchColorSettings, sliderColorSettings } = res;
            // @ts-ignore
            const { textSettings, _BISettings } = res;
            // @ts-ignore
            const { multiSwitchButtonStyle, numericFieldWriterButtonStyle, sliderButtonStyle } = res;
            _client.current = client;
            setSwitchColorSettings(switchColorSettings);
            const { panelType } = options;
            if (panelType === PanelType.MultiSwitch) {
              setButtonStyle(multiSwitchButtonStyle);
            } else if (panelType === PanelType.NumericFieldWriter) {
              setButtonStyle(numericFieldWriterButtonStyle);
            } else if (panelType === PanelType.Slider) {
              setButtonStyle(sliderButtonStyle);
            }
            setSliderColorSettings(sliderColorSettings);
            setTextSettings(textSettings);
            setBISettings(_BISettings);
            match = true;
          }
        });
      if (match) {
        break;
      }
    }
  }, [dataSources]);

  const customStyles = {
    button: {
      background: options?.overrideButtonColorSettings ? options?.activeButtonColor : buttonStyle?.activeButtonColor,
      color: options?.overrideButtonColorSettings ? options?.activeButtonTextColor : buttonStyle?.activeButtonTextColor,
      '&:disabled': {
        background: options?.overrideButtonColorSettings
          ? options?.inactiveButtonColor
          : buttonStyle?.inactiveButtonColor,
        color: options?.overrideButtonColorSettings
          ? options?.inactiveButtonTextColor
          : buttonStyle?.inactiveButtonTextColor,
      },
      '&:hover': {
        background: options?.overrideButtonColorSettings ? options?.hoverButtonColor : buttonStyle?.hoverButtonColor,
        color: options?.overrideButtonColorSettings ? options?.hoverButtonTextColor : buttonStyle?.hoverButtonTextColor,
      },
      fontSize: '16px',
      height: '40px',
    },
    slider: {
      color: sliderColorSettings.sliderColor,
    },
  };

  const { panelType, multiSwitchTab } = options;
  const styles = getStyles();

  return (
    <>
      <div
        className={cx(
          styles.wrapper,
          css`
            width: ${width}px;
            height: ${height}px;
          `
        )}
      >
        {panelType === PanelType.Slider && (
          <SliderPanel
            _client={_client}
            writable={writable}
            topicProtocols={topicProtocols}
            options={options}
            fieldConfig={fieldConfig.defaults}
            isRunning={isRunning}
            setIsRunning={setIsRunning}
            dataValue={value}
            customStyles={customStyles}
          />
        )}
        {panelType === PanelType.SingleStat && (
          <SingleStatPanel
            options={options}
            isRunning={isRunning}
            setIsRunning={setIsRunning}
            dataValue={value}
            textSettings={textSettings}
          />
        )}
        {panelType === PanelType.Switch && (
          <SwitchPanel
            _client={_client}
            topicProtocols={topicProtocols}
            isRunning={isRunning}
            setIsRunning={setIsRunning}
            dataValue={value}
            options={options}
            switchColorSettings={switchColorSettings}
          />
        )}
        {panelType === PanelType.MultiSwitch && (
          <MultiSwitchPanel
            _client={_client}
            topicProtocols={topicProtocols}
            multiSwitchTab={multiSwitchTab}
            isRunning={isRunning}
            setIsRunning={setIsRunning}
            dataValue={value}
            customStyles={customStyles}
          />
        )}
        {panelType === PanelType.NumericFieldWriter && (
          <NumericFieldWriterPanel
            _client={_client}
            writable={writable}
            topicProtocols={topicProtocols}
            options={options}
            fieldConfig={fieldConfig.defaults}
            isRunning={isRunning}
            setIsRunning={setIsRunning}
            dataValue={value}
            customStyles={customStyles}
          />
        )}
        {isRunning && (
          <div className={styles.spinner}>
            <Spinner size={12} />
          </div>
        )}
      </div>
      {backgroundImageURL && (
        <div
          style={{
            position: 'absolute',
            zIndex: Z_INDEX_BACKGROUND,
            bottom: `${yPosition}%`,
            transform: `translateX(-${xPosition}%) translateY(${yPosition}%)`,
            left: `${xPosition}%`,
            opacity: opacity / 100,
            width: `${scale}%`,
            height: `${scale}%`,
            backgroundImage: `url(${backgroundImageURL})`,
            backgroundSize: 'contain',
            backgroundRepeat: 'no-repeat',
            backgroundPosition: `${xPosition}% ${100 - yPosition}%`,
          }}
        />
      )}
      {!writable && <div className={styles.overlay} />}
    </>
  );
};

const getStyles = stylesFactory(() => {
  return {
    wrapper: css`
      position: relative;
      z-index: ${Z_INDEX_WRITER};
    `,
    overlay: css`
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: ${Z_INDEX_OVERLAY};
      transparent: 100%;
    `,
    spinner: css`
      position: absolute;
      top: -32px;
      right: -2px;
    `,
  };
});
