import * as React from 'react';
import { PanelPlugin } from '@grafana/data';
import { CategoryType, Image, PanelType, SimpleOptions } from './types';
import { BasePanel } from './BasePanel';
import { MultiSwitchTab } from './fields/MultiSwitchTab';
import { SliderEditor } from './fields/SliderEditor';
import { ColorPicker } from '@grafana/ui';

export const plugin = new PanelPlugin<SimpleOptions>(BasePanel)
  .setPanelOptions(builder => {
    const datePickerStyle = { display: 'flex', alignItems: 'center', height: '32px' };

    return builder
      .addSelect({
        path: 'panelType',
        name: 'Panel Type',
        settings: {
          options: [
            { value: PanelType.Slider, label: PanelType.Slider },
            { value: PanelType.SingleStat, label: PanelType.SingleStat },
            { value: PanelType.Switch, label: PanelType.Switch },
            { value: PanelType.MultiSwitch, label: PanelType.MultiSwitch },
            { value: PanelType.NumericFieldWriter, label: PanelType.NumericFieldWriter },
          ],
        },
        defaultValue: PanelType.Slider,
      })
      .addCustomEditor({
        id: 'multiSwitchTab',
        path: 'multiSwitchTab',
        name: 'Number of Count',
        category: [PanelType.MultiSwitch],
        editor: props => {
          return <MultiSwitchTab {...props} />;
        },

        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.panelType === PanelType.MultiSwitch;
        },
      })

      .addSelect({
        path: 'image',
        name: 'Image',
        settings: {
          options: [
            { value: Image.NoImage, label: Image.NoImage },
            { value: Image.Light, label: Image.Light },
            { value: Image.Fan, label: Image.Fan },
            { value: Image.Cooling, label: Image.Cooling },
            { value: Image.Heating, label: Image.Heating },
            { value: Image.Alert, label: Image.Alert },
          ],
        },
        defaultValue: Image.NoImage,
        category: [CategoryType.SingleStat],
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.panelType === PanelType.SingleStat;
        },
      })
      .addBooleanSwitch({
        path: 'showValue',
        name: 'Show Value (on Top)',
        defaultValue: true,
        category: [CategoryType.SingleStat],
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.panelType === PanelType.SingleStat && currentConfig.image !== Image.NoImage;
        },
      })
      .addBooleanSwitch({
        path: 'overrideValue',
        name: 'Override Value',
        defaultValue: false,
        category: [CategoryType.SingleStat],
        showIf: (currentConfig: SimpleOptions) => {
          return (
            currentConfig.panelType === PanelType.SingleStat &&
            currentConfig.image !== Image.NoImage &&
            currentConfig.showValue
          );
        },
      })
      .addTextInput({
        path: 'trueValue',
        name: 'True Value',
        defaultValue: '',
        category: [CategoryType.SingleStat],
        showIf: (currentConfig: SimpleOptions) => {
          return (
            currentConfig.panelType === PanelType.SingleStat &&
            currentConfig.image !== Image.NoImage &&
            currentConfig.showValue &&
            currentConfig.overrideValue
          );
        },
      })
      .addTextInput({
        path: 'falseValue',
        name: 'False Value',
        defaultValue: '',
        category: [CategoryType.SingleStat],
        showIf: (currentConfig: SimpleOptions) => {
          return (
            currentConfig.panelType === PanelType.SingleStat &&
            currentConfig.image !== Image.NoImage &&
            currentConfig.showValue &&
            currentConfig.overrideValue
          );
        },
      })

      .addBooleanSwitch({
        path: 'overrideTextSettings',
        name: 'Override Text Settings',
        defaultValue: false,
        category: [CategoryType.TextSettings],
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.panelType === PanelType.SingleStat;
        },
      })
      .addNumberInput({
        path: 'textSize',
        name: 'Text Size',
        defaultValue: 40,
        category: [CategoryType.TextSettings],
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.panelType === PanelType.SingleStat && currentConfig.overrideTextSettings;
        },
      })
      .addNumberInput({
        path: 'unitSize',
        name: 'Unit Size',
        defaultValue: 20,
        category: [CategoryType.TextSettings],
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.panelType === PanelType.SingleStat && currentConfig.overrideTextSettings;
        },
      })
      .addCustomEditor({
        id: 'unitColor',
        path: 'unitColor',
        name: 'Unit Color',
        defaultValue: '#000',
        category: [CategoryType.TextSettings],
        editor: props => {
          return (
            <div style={datePickerStyle}>
              <ColorPicker
                color={props.value}
                onChange={color => {
                  props.onChange(color);
                }}
              />
            </div>
          );
        },
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.panelType === PanelType.SingleStat && currentConfig.overrideTextSettings;
        },
      })

      .addBooleanSwitch({
        path: 'overrideSwitchColorSettings',
        name: 'Override Switch Color Settings',
        defaultValue: false,
        category: [CategoryType.SwitchColorSettings],
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.panelType === PanelType.Switch;
        },
      })
      .addCustomEditor({
        id: 'switchTrueColor',
        path: 'switchTrueColor',
        name: 'True Color',
        defaultValue: '#52D869',
        category: [CategoryType.SwitchColorSettings],
        editor: props => {
          return (
            <div style={datePickerStyle}>
              <ColorPicker
                color={props.value}
                onChange={color => {
                  props.onChange(color);
                }}
              />
            </div>
          );
        },
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.panelType === PanelType.Switch && currentConfig.overrideSwitchColorSettings;
        },
      })
      .addCustomEditor({
        id: 'switchFalseColor',
        path: 'switchFalseColor',
        name: 'False Color',
        defaultValue: '#7E7E7E',
        category: [CategoryType.SwitchColorSettings],
        editor: props => {
          return (
            <div style={datePickerStyle}>
              <ColorPicker
                color={props.value}
                onChange={color => {
                  props.onChange(color);
                }}
              />
            </div>
          );
        },
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.panelType === PanelType.Switch && currentConfig.overrideSwitchColorSettings;
        },
      })

      .addBooleanSwitch({
        path: 'overrideButtonColorSettings',
        name: 'Override Button Color Settings',
        defaultValue: false,
        category: [CategoryType.ButtonColorSettings],
        showIf: (currentConfig: SimpleOptions) => {
          return [PanelType.Slider, PanelType.MultiSwitch, PanelType.NumericFieldWriter].includes(
            currentConfig.panelType
          );
        },
      })
      .addCustomEditor({
        id: 'activeButtonColor',
        path: 'activeButtonColor',
        name: 'Active Button Color',
        defaultValue: '#303F9F',
        category: [CategoryType.ButtonColorSettings],
        editor: props => {
          return (
            <div style={datePickerStyle}>
              <ColorPicker
                color={props.value}
                onChange={color => {
                  props.onChange(color);
                }}
              />
            </div>
          );
        },
        showIf: (currentConfig: SimpleOptions) => {
          return (
            [PanelType.Slider, PanelType.MultiSwitch].includes(currentConfig.panelType) &&
            currentConfig.overrideButtonColorSettings
          );
        },
      })
      .addCustomEditor({
        id: 'activeButtonTextColor',
        path: 'activeButtonTextColor',
        name: 'Active Button Text Color',
        defaultValue: '#E0E0E0',
        category: [CategoryType.ButtonColorSettings],
        editor: props => {
          return (
            <div style={datePickerStyle}>
              <ColorPicker
                color={props.value}
                onChange={color => {
                  props.onChange(color);
                }}
              />
            </div>
          );
        },
        showIf: (currentConfig: SimpleOptions) => {
          return (
            [PanelType.Slider, PanelType.MultiSwitch].includes(currentConfig.panelType) &&
            currentConfig.overrideButtonColorSettings
          );
        },
      })
      .addCustomEditor({
        id: 'inactiveButtonColor',
        path: 'inactiveButtonColor',
        name: 'Inactive Button Color',
        defaultValue: '#E0E0E0',
        category: [CategoryType.ButtonColorSettings],
        editor: props => {
          return (
            <div style={datePickerStyle}>
              <ColorPicker
                color={props.value}
                onChange={color => {
                  props.onChange(color);
                }}
              />
            </div>
          );
        },
        showIf: (currentConfig: SimpleOptions) => {
          return (
            [PanelType.Slider, PanelType.MultiSwitch].includes(currentConfig.panelType) &&
            currentConfig.overrideButtonColorSettings
          );
        },
      })
      .addCustomEditor({
        id: 'inactiveButtonTextColor',
        path: 'inactiveButtonTextColor',
        name: 'Inactive Button Text Color',
        defaultValue: '#AEAEAE',
        category: [CategoryType.ButtonColorSettings],
        editor: props => {
          return (
            <div style={datePickerStyle}>
              <ColorPicker
                color={props.value}
                onChange={color => {
                  props.onChange(color);
                }}
              />
            </div>
          );
        },
        showIf: (currentConfig: SimpleOptions) => {
          return (
            [PanelType.Slider, PanelType.MultiSwitch].includes(currentConfig.panelType) &&
            currentConfig.overrideButtonColorSettings
          );
        },
      })
      .addCustomEditor({
        id: 'hoverButtonColor',
        path: 'hoverButtonColor',
        name: 'Hover Button Color',
        defaultValue: '#30409E',
        category: [CategoryType.ButtonColorSettings],
        editor: props => {
          return (
            <div style={datePickerStyle}>
              <ColorPicker
                color={props.value}
                onChange={color => {
                  props.onChange(color);
                }}
              />
            </div>
          );
        },
        showIf: (currentConfig: SimpleOptions) => {
          return (
            [PanelType.Slider, PanelType.MultiSwitch].includes(currentConfig.panelType) &&
            currentConfig.overrideButtonColorSettings
          );
        },
      })
      .addCustomEditor({
        id: 'hoverButtonTextColor',
        path: 'hoverButtonTextColor',
        name: 'Hover Button Text Color',
        defaultValue: '#E0E0E0',
        category: [CategoryType.ButtonColorSettings],
        editor: props => {
          return (
            <div style={datePickerStyle}>
              <ColorPicker
                color={props.value}
                onChange={color => {
                  props.onChange(color);
                }}
              />
            </div>
          );
        },
        showIf: (currentConfig: SimpleOptions) => {
          return (
            [PanelType.Slider, PanelType.MultiSwitch].includes(currentConfig.panelType) &&
            currentConfig.overrideButtonColorSettings
          );
        },
      })

      .addBooleanSwitch({
        path: 'overrideSliderSettings',
        name: 'Override Slider Settings',
        defaultValue: false,
        category: [CategoryType.SliderColorSettings],
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.panelType === PanelType.Slider;
        },
      })
      .addCustomEditor({
        id: 'sliderColor',
        path: 'sliderColor',
        name: 'Color',
        defaultValue: '#303F9F',
        category: [CategoryType.SliderColorSettings],
        editor: props => {
          return (
            <div style={datePickerStyle}>
              <ColorPicker
                color={props.value}
                onChange={color => {
                  props.onChange(color);
                }}
              />
            </div>
          );
        },
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.panelType === PanelType.Slider && currentConfig.overrideSliderSettings;
        },
      })

      .addTextInput({
        path: 'backgroundImageURL',
        name: 'Background Image URL',
        defaultValue: '',
        category: [CategoryType.Background],
      })
      .addBooleanSwitch({
        path: 'overrideBISettings',
        name: 'Override Background Image Settings',
        defaultValue: false,
        category: [CategoryType.Background],
      })
      .addCustomEditor({
        id: 'opacity',
        path: 'opacity',
        name: 'Opacity',
        category: [CategoryType.Background],
        defaultValue: 30,
        editor: props => {
          return <SliderEditor {...props} />;
        },
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.overrideBISettings;
        },
      })
      .addCustomEditor({
        id: 'scale',
        path: 'scale',
        name: 'Scale',
        category: [CategoryType.Background],
        defaultValue: 30,
        editor: props => {
          return <SliderEditor {...props} />;
        },
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.overrideBISettings;
        },
      })
      .addCustomEditor({
        id: 'xPosition',
        path: 'xPosition',
        name: 'X-Position',
        category: [CategoryType.Background],
        defaultValue: 100,
        editor: props => {
          return <SliderEditor {...props} />;
        },
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.overrideBISettings;
        },
      })
      .addCustomEditor({
        id: 'yPosition',
        path: 'yPosition',
        name: 'Y-Position',
        category: [CategoryType.Background],
        defaultValue: 0,
        editor: props => {
          return <SliderEditor {...props} />;
        },
        showIf: (currentConfig: SimpleOptions) => {
          return currentConfig.overrideBISettings;
        },
      });
  })
  .useFieldConfig({});
